import React from 'react'
import {Box, Button, Grid, Paper} from '@mui/material';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward'; //
import ArrowOutwardIcon from '@mui/icons-material/ArrowOutward'; //
import ArrowForwardIcon from '@mui/icons-material/ArrowForward'; //
import SouthEastIcon from '@mui/icons-material/SouthEast'; //
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward'; //
import SouthWestIcon from '@mui/icons-material/SouthWest';//
import ArrowBackIcon from '@mui/icons-material/ArrowBack'; //
import NorthWestIcon from '@mui/icons-material/NorthWest'; //
import SyncAltIcon from '@mui/icons-material/SyncAlt';
import SubOutPrm from './SubOutPrm';
import { styled } from '@mui/material/styles';

function CtrlDpad8(
  props:{
    valueUp: any,
    valueUpRithg: any,
    valueRight: any,
    valueRightDown: any,
    valueDown: any,
    valueLeftDown: any,
    valueLeft: any,
    valueLeftUp: any,
    valueLeftRight: any,
    valueUpDown: any,
  }) 
{

  const {
    valueUp,
    valueUpRithg,
    valueRight,
    valueRightDown,
    valueDown,
    valueLeftDown,
    valueLeft,
    valueLeftUp,
    valueLeftRight,
    valueUpDown,
  } = props;

  //
  const pram = {
    //
    fontSize:"10px",
    //
    Button_w:"40px",
    Button_h:"40px",
    //8
    Param_w:"40px",
    Param_h:"40px",
    //
    ParamOut_w:"80px",
    ParamOut_h:"40px",
  };
  
  const PaperSx = {
    display: 'flex',
    justifyContent: 'center', //
    alignItems: 'center', //
    height: pram.Button_h,
    width: pram.Button_w,
    padding: '0px',
    borderRadius: 0,
    backgroundColor: 'rgba(0, 0, 0, 0)',
//    color: 'rgba(0, 0, 0, 0)',
//    border:'none',
    boxShadow: 'none',
  };

  const PaperOutSx = {
    display: 'flex',
    justifyContent: 'left', //
    alignItems: 'center', //
    height: pram.Button_h,
    width: pram.Button_w,
    padding: '0px',
    borderRadius: 0,
    backgroundColor: 'rgba(0, 0, 0, 0)',
//    color: 'rgba(0, 0, 0, 0)',
//    border:'none',
    boxShadow: 'none',
  };

  const ButtonSx = {
    fontSize: pram.fontSize,
    fontFamily: 'Courier New, monospace',
    borderRadius: 0,
    height: pram.Button_h,
    width: pram.Button_w,
    textAlign: 'center',
    padding: '0px',
    minWidth: '0px',
    backgroundColor:'gray',
    '&:hover': {
      backgroundColor: 'gray',
      filter: 'brightness(0.8)',
      border:'none',
    },
  };

  const SyncAltIcon90 = styled(SyncAltIcon)({
    transform: 'rotate(90deg)'
  });

  return (
          <Grid container spacing={0}>
            <Grid container spacing={0.3} sx={{mb:0.1}}>
              <Grid item>
                <Paper sx={PaperSx}><SubOutPrm value={valueLeftUp} sx={{color: "black", height: pram.Param_h, width:pram.Param_w}}/></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}>2</Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><SubOutPrm value={valueUp} sx={{color: "black", height: pram.Param_h, width:pram.Param_w}}/></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}>4</Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><SubOutPrm value={valueUpRithg} sx={{color: "black", height: pram.Param_h, width:pram.Param_w}}/></Paper>
              </Grid>
            </Grid>
            <Grid container spacing={0.3} sx={{mb:0.3}}> 
              <Grid item>
                <Paper sx={PaperSx}>6</Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><Button sx={ButtonSx} variant="contained"><NorthWestIcon /></Button></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><Button sx={ButtonSx} variant="contained"><ArrowUpwardIcon /></Button></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><Button sx={ButtonSx} variant="contained"><ArrowOutwardIcon /></Button></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}>10</Paper>
              </Grid>
            </Grid>
            <Grid container spacing={0.3} sx={{mb:0.3}}>
              <Grid item>
                <Paper sx={PaperSx}><SubOutPrm value={valueLeft} sx={{color: "black", height: pram.Param_h, width:pram.Param_w}}/></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><Button sx={ButtonSx} variant="contained"><ArrowBackIcon /></Button></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}>13</Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><Button sx={ButtonSx} variant="contained"><ArrowForwardIcon /></Button></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><SubOutPrm value={valueRight} sx={{color: "black", height: pram.Param_h, width:pram.Param_w}}/></Paper>
              </Grid>
            </Grid>

            <Grid container spacing={0.3} sx={{mb:0.3}}> 
              <Grid item>
                <Paper sx={PaperSx}>16</Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><Button sx={ButtonSx} variant="contained"><SouthWestIcon /></Button></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><Button sx={ButtonSx} variant="contained"><ArrowDownwardIcon /></Button></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><Button sx={ButtonSx} variant="contained"><SouthEastIcon /></Button></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}>20</Paper>
              </Grid>
            </Grid>

            <Grid container spacing={0.3} sx={{mb:1.0}}> 
              <Grid item>
                <Paper sx={PaperSx}><SubOutPrm value={valueLeftDown} sx={{color: "black", height: pram.Param_h, width:pram.Param_w}}/></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}>22</Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><SubOutPrm value={valueDown} sx={{color: "black", height: pram.Param_h, width:pram.Param_w}}/></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}>24</Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><SubOutPrm value={valueRightDown} sx={{color: "black", height: pram.Param_h, width:pram.Param_w}}/></Paper>
              </Grid>
            </Grid>

            <Grid container spacing={0.3} sx={{mb:0.1}}> 
              <Grid item>
                <Paper sx={PaperSx}></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><SyncAltIcon /></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperOutSx}><SubOutPrm value={valueLeftRight} sx={{color: "black", height: pram.ParamOut_h, width:pram.ParamOut_w}}/></Paper>
              </Grid>
            </Grid>

            <Grid container spacing={0.3}> 
              <Grid item>
                <Paper sx={PaperSx}></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperSx}><SyncAltIcon90 /></Paper>
              </Grid>
              <Grid item>
                <Paper sx={PaperOutSx}><SubOutPrm value={valueUpDown} sx={{color: "black", height: pram.ParamOut_h, width:pram.ParamOut_w}}/></Paper>
              </Grid>
            </Grid>

          </Grid>
  )
}

export default CtrlDpad8
