/* 
  
*/
import React from 'react';
import { Box, Grid, Paper } from '@mui/material';
import CtrlClutch from './CtrlClutch';
import CtrlAccel from './CtrlAccel';
import CtrlDpad from './CtrlDpad';
import CtrlButton from './CtrlButton';
import CtrlEStop from './CtrlEStop';
import CtrlDpad8 from './CtrlDpad8';

function Controller() {
  return (
    <Grid container spacing={1} sx={{ height: '100%' }}>
      <Grid item style={{ width: '550px', height: '100%' }}>
        <Paper sx={{ backgroundColor: 'lightblue', height: '100%' }}>

          {/*  */}
          <Grid container sx={{width: '100%',mb:1}}>
            <Grid item xs></Grid>
            <Grid item sx={{width:'150px'}}>
              {/*  */}
              <CtrlClutch value={"0"}/>
            </Grid>
            <Grid item sx={{width:'70px'}}></Grid>
            <Grid item sx={{width:'150px'}}>
              {/*  */}
              <CtrlAccel value={"0"}/>
            </Grid>
            <Grid item xs></Grid>
          </Grid>

          {/*  */}
          <Grid container 
            sx={{
                  width: '100%',
                  //border:"1px black solid",
                  mb:1,
                }}
          >
            <Grid item xs></Grid>
            <Grid item 
                  sx={{
                        height:'100%',width:"220px",
                        //border:"1px solid black",
                        display:'flex',
                        alignItems: 'center', // 
                        justifyContent: 'center' // 
                      }}
            >
              {/*  */}
              <CtrlDpad valueUp={"0"} valueRight={"0"} valueDown={"0"} valueLeft={"0"}/>
            </Grid>
            <Grid item sx={{width:'30px'}}></Grid>
            <Grid item 
                  sx={{
                        height:'100%',width:"215px",
                        //border:"1px solid black",
                        display:'flex',
                        alignItems: 'center', // 
                        justifyContent: 'center' // 
                      }}
            >
              {/*  */}
              <CtrlButton value={"0"}/>
            </Grid>
            <Grid item xs></Grid>
          </Grid>

          {/*  */}
          <Grid container sx={{width: '100%',mb:1}}>
            <Grid item xs></Grid>
            <Grid item 
                  sx={{
                        //border:"solid black 1px",
                        width:'205px',
                        display:'flex',
                        alignItems: 'center', // 
                        justifyContent: 'center' // 
                      }}>
              {/*  */}
              <CtrlEStop value={"0"}/>
            </Grid>
            <Grid item xs></Grid>
          </Grid>

          {/*  */}
          <Grid container 
            sx={{
                  width: '100%',
                  //border:"1px black solid",
                  mb:1,
                }}
          >
            <Grid item xs></Grid>
            <Grid item 
                  sx={{
                        height:'100%',width:"220px",
                        //border:"1px solid black",
                        display:'flex',
                        alignItems: 'center', // 
                        justifyContent: 'center' // 
                      }}
            >

              {/* 8 */}
              <CtrlDpad8 
              valueUp={"UP"}
              valueUpRithg={"UR"}
              valueRight={"R"}
              valueRightDown={"RD"}
              valueDown={"D"}
              valueLeftDown={"LD"}
              valueLeft={"L"}
              valueLeftUp={"LU"}
              valueLeftRight={"LRLR"}
              valueUpDown={"DWDW"}
              />
            </Grid>
            <Grid item sx={{width:'30px'}}></Grid>
            <Grid item 
                  sx={{
                        height:'100%',width:"215px",
                        //border:"1px solid black",
                        display:'flex',
                        alignItems: 'center', // 
                        justifyContent: 'center' // 
                      }}
            >
              {/* 8 */}
              <CtrlDpad8 
              valueUp={"UP"}
              valueUpRithg={"UR"}
              valueRight={"R"}
              valueRightDown={"RD"}
              valueDown={"D"}
              valueLeftDown={"LD"}
              valueLeft={"L"}
              valueLeftUp={"LU"}
              valueLeftRight={"LRLR"}
              valueUpDown={"DWDW"}
              />
            </Grid>
            <Grid item xs></Grid>
          </Grid>

        </Paper>
      </Grid>
      <Grid item style={{ width: '200px', height: '100%' }}>
        <Paper sx={{ backgroundColor: 'lightgreen', height: '100%' }}>
          2: 200px
        </Paper>
      </Grid>
      <Grid item xs sx={{ height: '100%' }}>
        <Paper sx={{ backgroundColor: 'lightcoral', height: '100%' }}>
          3: 
        </Paper>
      </Grid>
    </Grid>
  );
}

export default Controller;
